.HQcontainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.HQtopContainer {
  height: calc(100% - 290px);
  flex-wrap: nowrap;
  flex-shrink: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 50px 15px 60px;
  background-color: rgba(255, 255, 255, 0.03);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.HQconsole {
  margin: 0px 35px 0px 0px;
  width: 68%;
}
.HQcontrols {
  flex-wrap: nowrap;
  flex-shrink: 0;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.HQtimelineBox {
  width: 100%;
  padding: 5px 20px 10px 5px;
  display: block;
  flex-wrap: nowrap;
  flex-shrink: 0;
  height: 240px;
  position: absolute;
  bottom: 0px;
}
.HQpinBox {
  display: inline-block;
  min-width: 80px;
}
.HQpinUnit {
  padding: 0px 0px 10px 0px;
  min-width: 80px;
  vertical-align: middle;
  /*background-color: green;*/
}
.HQvSliderUnit {
  float: left;
  height: 190px;
  text-align: center;
}
.HQvSliderTitle {
  position: relative;
  top: -10px;
}
.HQvSliderBox {
  display: inline-block;
  min-width: 90px;
}
.HQdialBox {
  text-align: center;
  padding: 0px 0px 5px 10px;
}
.HQcaptureIconUnit {
  display: block;
  margin: auto;
  font-size: 1.1em;
  position: relative;
  top: 5px;
}
.HQcaptureButtonText {
  color: rgba(61, 153, 255, 0.75);
}
.HQpageTitle {
  color: rgba(255, 255, 255, 0.65);
  font-size: 1.5em;
  position: relative;
  top: 5px;
}
.HQtimelineButtonRow {
  padding: 0px 10px 0px 55px;
  position: relative;
  top: 2px;
  flex-wrap: nowrap;
}
.HQtimelineButtonUnit {
  padding: 0px 10px 0px 10px;
}
.HQbuttonText {
  color: rgba(255, 255, 255, 0.45);
  font-size: 1em;
  /*left: -15px;*/
}
@media only screen and (max-width: 600px) {
  .HQcontainer {
    position: static;
    justify-content: flex-start;
  }
  .HQtopContainer {
    padding: 15px 30px 20px 35px;
    background-color: rgba(255, 255, 255, 0);
  }
  .HQtimelineButtonRow {
    padding: 0px 5px 0px 40px;
  }
  .HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
  }
}
