.HQcontainer {
  width: 100%;
  height: 100%;
  // height: calc(100% - 100px);
  display: flex;
  // justify-content: center;
  justify-content: flex-start;
  flex-direction: column;
  // position: absolute;
  // bottom: 0px;  
  // background: @loGray;
}

.HQtopContainer {
  // min-height: 200px;
  // height: 100%;
  height: calc(100% - 290px);
  // height: 500px;
  flex-wrap: nowrap;
  flex-shrink: 0; 
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 50px 15px 60px; 
  background-color: fade(white, 3%);
  border-bottom: 1px solid fade(white, 10%);  
}

.HQconsole {
  // background: pink;
  margin: 0px 35px 0px 0px;
  width: 68%;
}

.HQcontrols {
  flex-wrap: nowrap;
  flex-shrink: 0;  
  // width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  // padding: 10px 25px 15px 25px;  
  // background: rgba(0, 0, 0, 0.6); 
  // border-radius: 5px;
  // padding: 20px;
}

.HQtimelineBox {
  width: 100%;
  padding: 5px 20px 10px 5px;
  display: block;
  flex-wrap: nowrap;
  flex-shrink: 0; 
  height: 240px;
  // background: pink;
  // margin-top: 10px;
  // background: fade(white, 3%);
  position: absolute;
  bottom: 0px;
}

.HQpinBox {
  display: inline-block;
  min-width: 80px;
//   height: 190px;
//   background: blue;
}

.HQpinUnit {
  padding: 0px 0px 10px 0px;
  min-width: 80px;
  vertical-align: middle;
  /*background-color: green;*/
}

.HQvSliderUnit {
  float: left;
  height: 190px;
  // padding: 0px 0px 20px 0px;
  text-align: center;
  // background: green;
}

.HQvSliderTitle {
  position: relative;
  top: -10px;
}

.HQvSliderBox {
  display: inline-block;
//   height: 190px;
  min-width: 90px;
//   background: green;
}

.HQdialBox {
  text-align: center;
//   height: 190px;
  padding: 0px 0px 5px 10px;
//   background: yellow;
}

.HQcaptureIconUnit {
  display: block;
  margin: auto;
  font-size: 1.10em;
  position: relative;
  top: 5px;
//   background: yellow;
}

.HQcaptureButtonText {
    color: @hiBlue;
}

.HQpageTitle {
  color:@hiGray;  
  font-size: 1.5em;
  position: relative;
  top: 5px;
}

.HQtimelineButtonRow {
  padding: 0px 10px 0px 55px;
  position: relative;
  top: 2px;
  flex-wrap: nowrap;
}

.HQmoSelector {
  // padding-left: 58px;
}

.HQtimelineButtonBox {

}

.HQtimelineButtonUnit {
    padding: 0px 10px 0px 10px;
    // background-color: yellow;
}

.HQbuttonText {
    color: @midGray;
    font-size: 1em;
    /*left: -15px;*/
}


@media only screen and (max-width: 600px) {
  .HQcontainer {
    position: static;
      justify-content: flex-start;
  }
  .HQtopContainer {  
    padding: 15px 30px 20px 35px; 
    background-color: fade(white, 0%);    
  }
  .HQtimelineButtonRow {
    padding: 0px 5px 0px 40px;
  }
  .HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
  }  
}
